body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
html, body, div#root {
  height: 100%;
  margin: 0;
  padding: 0;
}
body * {
  box-sizing: border-box;
}

:root {
  --size-light: .1rem;
  --size-normal: .25rem;
  --size-hard: .5rem;
  --size-great: 1rem;
  --size-titan: 2rem;
  --size-border: .15rem;

  --transition: .5s;
}