main {
    height: 100%;
    width: 100%;
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
    /*background: rgba(30,30,30,1) url('https://allods.mail.ru/media/originals/52b07a233f8d5.jpg') no-repeat fixed center/cover;*/
    background: rgba(30,30,30,1) url('https://psv4.userapi.com/c848124/u280095054/docs/d10/2553d24de4df/Video_Trim.gif?extra=AIkPV4U9NaA9hfMDgSU_klpnvqWhNodvv6ppPm2dZ1q_GDNf0lG4LDxum43Ku4fjuJ06nXh7SYzF95KjazbWvYbKikkbmeXP58_w-MxBjaYXLMC0Cx12XHmk8MkVWzrjxbov0kbmVOcavzMumPMxZOI') no-repeat fixed center/cover;
}
.block {
    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-start;
    align-items: center;
    background: url('./MainFrame.png') no-repeat scroll center/100% 100%;
    filter: drop-shadow(.5rem 1rem .5rem black);
    font-family: AllodsWest;
    color: #dadada;
    cursor: default;
}
.carousel {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    align-items: stretch;
    gap: var(--size-hard, 1rem);
    padding: var(--size-great, 1rem);
}
.carousel div.element {
    min-height: 300px;
    min-width: 250px;
    max-width: 400px;
    border-radius: var(--size-hard, .5rem);
    background-color: gray;
    background-repeat: no-repeat;
    background-attachment: scroll;
    background-position: center;
    background-size: cover;
    padding: var(--size-hard,.5rem);
    transition: var(--transition);
}
.carousel div.element:hover {
    transform: translateY(-.5rem);
}
